.dessert{
    position: relative;
}

.dessert:before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #304352;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #d7d2cc, #304352);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #d7d2cc, #304352); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    z-index: -1;
}

.row{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
    
}  
    .col-3{
        display: flex;
        flex-direction: column;
        width: 30%;
        border: 1px solid black;
        margin: 10px 0;
        border-radius: 7px;
        justify-content: space-between;
    }
    .product{
       width: 100%;
    /* margin: 40px 0 40px 0; */
    }
    /* .image{
      
       margin-bottom: 10px;
    } */
    .image img{
       width: 100%;
       height: 100%;
       border-radius: 7px;
    }
 
    .info{
        display: flex;
        flex-direction: column;
        align-items: center;
       
    }
    
    .info h3{
       font-size: 20px;
       margin-bottom: 0;
    }
    .rating{
       margin-bottom: 0;
       padding-left: 0;
       list-style: none;
    }
    .rating li{
       display: inline-block;
       color:yellow
    }
    .info_price{
        width: 70%;
        margin-bottom: 10px;
       display: flex;
       flex-flow: row nowrap;
       justify-content: space-between;
       align-items: center;
    }
    .add-to-card{
       border: none;
       width: 40px;
       height: 40px;
       display: flex;
       align-items: center;
       justify-content: center;
       background-color: green;
       color: white;
       border-radius: 10px;
    }
    .price{
       font-size: 18px;
       font-weight: 700;
    }